import React from "react";
import "./Pricing.css"
import "./Home.css"
import Navigation from './Navigation';
import Typist from 'react-typist';
// import Gallery from 'react-photo-gallery'
// import pics from "./pics";
function Pricing() {
    
  return (
    

      
    <div>
      <div className="navd"> <Navigation/> </div>
        <span className= 'header-bar'> 
              <h1> Pricing</h1>
              <Typist  cursor= {{show: false}} >
              <h2>AutoTract's Pricing</h2>
            </Typist>
          </span>
      <div className="block">
        <div className="text">
          PERCENTAGE OF YOUR CONTRACT THAT YOUR AVERAGE AGENT TAKES:
          </div>
        <span className="percent">4%</span>
      </div>

      <div className="block">
        <div className="text">
          PERCENTAGE OF YOUR CONTRACT THAT AUTOTRACT TAKES:
          </div>
        <span className="percent">1.5%</span>
      </div>
      
      <div className="block last">
        <div className="text">
          FOR AGENTS WISHING TO USE AUTOTRACT:
          </div>
        <span className="percent">1%</span>
      </div>
      

    </div>
    
  );
}

export default Pricing;