import React from "react";
import "./Pricing.css"
import "./Home.css"
import "./Signup.css";
import Navigation from './Navigation';
function Signup(){
    
  return (
    

      
    <div>
      <div className="nav"> <Navigation/> </div>
        <span className= 'header-bar pad'> 
              <span className = 'header-bar-title smaller'> Sign Up/Login</span>
          </span>
        <div>
            <form className= "block">
                <p className= "text">Enter username:</p>
                <input type="text"/>
                <p className= "text">Enter password:</p>
                <input type="text"/>
                <p className= "text">Confirm password:</p>
                <input type="text"/>
            </form>
        </div>
        

    </div>
    
  );
}

export default Signup;