import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Navigation, Home,Pricing, Facts, Signup} from "./components";
function App() {
  return (
    <div className="App">
      <Router>
        {/* <Navigation/> */}
        <Switch>
          <Route path="/autotract" exact component={Home} />
          <Route path="/autotract/Pricing" exact component={Pricing} />
          <Route path="/autotract/Facts" exact component={Facts} />
          <Route path="/autotract/Signup" exact component={Signup} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;