import React from 'react'
import { Link } from 'react-router-dom';
import Typist from 'react-typist'
import "./Home.css";
import AutoTract from './AutoTract Logo.png'
import Navigation from './Navigation';

function Home() {
    return(
      <div>
        <div id = "n" class= "nav"><Navigation/></div>
        <div>
          <span className= 'header-bar'> 
            
            <h1 >AutoTract</h1>
            <Typist  cursor= {{show: false}} >
              <h2>A Machine Learning Solution to Sports Contract Analysis.</h2>
            </Typist>
          </span>
          
        </div>
        <div className="col">
          <span className="textbox-1">
            <h1>Cut Costs.</h1>
            <span>Autotract cuts the cost of legal expenses <b>massively</b>, offering a streamline solution to a costly and inefficient process</span>
          </span>
          <span className="textbox-2">
            <h1>Efficient returns</h1>
            <span>Autotract can analyze contracts and return them in under 36 hours, compared to the hundreds of hours required for an agent</span>
          </span>
          <span className="textbox-3">
            <h1>Smooth Experience</h1>
            <span>Autotract has been simplified and catered towards the users and is therefore simple and easy for anyone to use </span>
          </span>
        </div>
        <div className="footer">
          AutoTract 2021. 
        </div>
      </div>
    );
  }

  export default Home